import { animate, state, style, transition, trigger } from '@angular/animations';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, IonContent, IonInfiniteScroll, ModalController } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';
import { Observable } from 'rxjs';
import { ModalMapasComponent } from 'src/app/Components/modal-mapas/modal-mapas.component';
import { InicioService } from 'src/app/providers/Almacenamiento/inicio.service';
import { PedidosService } from 'src/app/providers/Api/Pedidos.service';
import { ToastServices } from 'src/app/providers/Utilidades/ToastService.service';
import * as moment from 'moment' ;
@Component({
  selector: 'app-salas-inicio',
  templateUrl: './salas-inicio.page.html',
  styleUrls: ['./salas-inicio.page.scss'],
  animations: [
    trigger('escribiendo', [
      state('activo', style({
        opacity: .5
      })),
      state('noactivo', style({
        opacity: 1
      })),
      transition('activo <=> noactivo', [
        animate('300ms')
      ])
    ])
  ]
})
export class SalasInicioPage implements OnInit {
  configurando = false;
  usuario = '';
  habilitado = false;
  cargado = false;
  id = '';

  salas = [];
  desde = 0;
  patron = '';
  scroll = true;
  role = '';

  @ViewChild('inifinito', {static: false}) infinite: IonInfiniteScroll;
  @ViewChild('contenido', {static: false}) contenido: IonContent;

  constructor(
    private router: Router,
    private alert: AlertController,
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private apiPedidos: PedidosService,
    private socket: Socket,
    private modal : ModalController
  ) { 
    
    this.escucharEscritoss().subscribe((data: any) => {
      console.log('escribiendo...', data);
      if (data.exe) {
        this.salas.forEach((it) => {
           if (it._id === data.idsala) {
             console.log('existe')
             it.escribiendo = true;
             setTimeout(() => {
              it.escribiendo = false;
             }, 500);
           }
        })

       }
     })



    this.escucharMensaje().subscribe(async(data: any) => {
      console.log('mensaje', data);
      if (data.exe) {


        const info = await this.almacenamiento.obtenerToken();

        if ( info ) {

          const temp = this.salas.filter((item) => {
            return item._id === data.resp.sala._id;
          })

          this.apiPedidos.mensajes(info.token, {idsala: temp[0]._id}).subscribe((resp: any) => {
              
            
              
            this.salas = this.salas.filter((item) => {
              return item._id !== data.resp.sala._id;
            })
            if (resp.exe) {

              temp[0].novistos = resp.response.length;
            }  else {
              temp[0].novistos = 0;
            }

            this.salas.unshift(temp[0]);
          }, (err) => {
            this.salas = this.salas.filter((item) => {
              return item._id !== data.resp.sala._id;
            })  
            temp[0].novistos = 0;
            this.salas.unshift(temp[0]);
          })
  
  
        
        }


       

       }
     })
 
  }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.pedidosListar();
  }

  refrescando(event) {
    this.pedidosListar().then(() => {
        event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }

  


  async pedidosListar() {
  
    const info = await this.almacenamiento.obtenerToken();
  
    if ( info ) {
        this.role = info.role;
        this.id = info._id;
        this.usuario = info.nombre;
        this.apiPedidos.misChats(info.token, {patron: '', desde: this.desde}).subscribe((data: any) => {
           // console.log(data)
          if (data.exe) {
            let f = data.response.length;
            data.response.forEach( async (item) => {
             item.escribiendo = false;
             this.socket.emit('iniciochat', {id: item._id});
             const l = await new Promise((resolve, reject) => {
               this.apiPedidos.mensajes(info.token, {idsala: item._id}).subscribe((resp: any) => {
              if (resp.exe) {
                console.log(resp, 'RESPUESTA DEL TODO')
                item.fechamsg = resp.response.lemgth > 0 ? resp.response[resp.response.length - 1] : item.fecha;
                item.novistos = resp.response.length;
                resolve(true);
              }  else {
                item.novistos = 0;
                resolve(true);
              }
            }, (err) => {
              item.novistos = 0;
              resolve(true);
            })

          });

          if (l) {
           
            f = f - 1;
            if (f === 0) {

              this.salas =  data.response;

              console.log(this.salas, 'salas', f)
            this.salas.sort((a, b) => {
              console.log(a.novistos)

              if (a.novistos < b.novistos) {
                console.log('meno')
                return 1;
              }
 
              if (a.novistos > b.novistos) {
               console.log('mayor')
               return -1;
             }
 
             return 0;
 
            })
          }
          this.cargado = true;
          }

           })

           if (data.response.length === 0) {
            this.cargado = true;
           }

           

         
         
          

          } else {
            this.toast.toastError('No se pudo consultar los pedidos, inténtelo más tarde');
            this.router.navigate(['/central/menu/'])
            this.cargado = true;
          }
         
        }, (err) => {
          this.toast.toastError('No se pudo consultar los pedidos, inténtelo más tarde');
          this.router.navigate(['/central/menu/'])
        })
      }
      
  }
 
  async crear() {
    const alert = await this.alert.create({
      header: 'Crear Pedido',
      // tslint:disable-next-line:max-line-length
      message: 'Los pedidos puedes realizarlos para comprar los repuestos o productos que necesites para tu vehículo.',
  
      buttons: [
        {
          text: 'Cancelar',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
           
          }
        },
        {
          text: 'Crear',
          cssClass: 'botonalerta',
          handler: (data) => {
            this.router.navigate(['/central/menu/control-mapas'])
          }
        }
      ]
    })

    await alert.present();
  }

  escucharEscritoss() {
      const myobserver = new Observable(observer => {
        this.socket.on('escribiendoChat', (data) => {
          observer.next(data);
        });
      });
    
      return myobserver;
    
  }

  escucharMensaje() {
    const myobserver = new Observable(observer => {
      this.socket.on('mensajeChat', (data) => {
        observer.next(data);
      });
    });
  
    return myobserver;
  
}


  entrar(sala) {
  
    this.almacenamiento.insertarChat(sala).then(() => {
      this.router.navigate(['/central/chats/chat']);
    }).catch(() => {
      this.toast.toastError('No se puede acceder al chat, inténtelo más tarde');
    })
  }


  
  async agregar(sala) {

    const modal = await this.modal.create({
      component: ModalMapasComponent,
      componentProps: { 
        sala
      }
    })

    await modal.present();
  }

  async quitar(id) {

    const alert = await this.alert.create({
      header: 'Atención!',
      // tslint:disable-next-line:max-line-length
      message: '¿Estas seguro de abandonar esta sala? No podras visualizar de nuevo la  información dentro de ella',
  
      buttons: [
        {
          text: 'Cancelar',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
           
          }
        },
        {
          text: 'Salir',
          cssClass: 'botonalerta',
          handler: async () => {
            const info = await this.almacenamiento.obtenerToken();

            if ( info ) {
        
                // tslint:disable-next-line:max-line-length
                let nuevo = this.salas.filter((item) => {
                  return item._id === id;
                  
                })[0].accesos;

                nuevo = nuevo.map((item) => {
                  return item;
                })
        
                nuevo = nuevo.filter((item) => {
                  return item !== info._id;
                })
                console.log(nuevo)
                this.apiPedidos.editarChat(info.token, {id, accesos: nuevo}).subscribe((data: any) => {
                   // console.log(data)
                    if (data.exe) {
                      this.toast.Especial('Saliste de la sala');
                      this.pedidosListar();
                    } else {
                      this.toast.toastError('No se pudo realizar la petición, inténtelo más tarde');
                    }
                }, (err) => {
                      this.toast.toastError('No se pudo realizar la petición, inténtelo más tarde');    
                })
              }
          }
        }
      ]
    })

    await alert.present();
  
  }


  async buscar(event) {
    // // console.log(event.detail, 'epale');
    
    if ( event.detail.value !== '') {
      this.desde = 0;
      this.infinite.disabled = false;
      this.patron = event.detail.value;

      const info = await this.almacenamiento.obtenerToken();

      if ( info ) {
        this.apiPedidos.misChats(info.token, {desde: this.desde,  patron: this.patron }).subscribe((data: any) => {
          // // console.log(data, 'mi data');
          if ( data.exe ){
            this.salas = data.response;
            if ( data.response.length === 0) {
              this.desde = 0;
            }
          } else {
            this.toast.toastError('No se pudo realizar la consulta, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la consulta, intentelo más tarde');
        })
      }
    } else {
      this.desde = 0;
      this.pedidosListar();
    }
  }


  async loadData( event ) {
    // event.target.complete()
    this.desde = this.desde + 15;

    const info = await this.almacenamiento.obtenerToken();

    if ( info ) {
      if ( this.patron === '') {
        this.apiPedidos.misChats(info.token, {desde: this.desde,  patron: this.patron }).subscribe((data: any) => {
          // // console.log(data, 'CARGADA OFICIAL');
          if (data.exe) {
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.salas.push(hoja);
              })
              event.target.complete();
             } else {
              event.target.disabled = true;
              this.desde = 0;
             }
          } else {
            this.toast.toastError('No se pudo traer los chats, inténtelo más tarde.');
          }
        }, (err) => {
          this.toast.toastError('No se pudo traer los chats, inténtelo más tarde.');
        })
      } else {
        // tslint:disable-next-line:max-line-length
        // // console.log(this.desde, 'pli');
        this.apiPedidos.misChats(info.token, {desde: this.desde,  patron: this.patron }).subscribe((data: any) => {
          // // console.log(data, 'mi data 2', this.desde);
          if ( data.exe ){
            if ( data.response.length > 0  ) {
              data.response.forEach((hoja) => {
                this.salas.push(hoja);
              })
              event.target.complete();
           } else {
            event.target.disabled = true;
            this.desde = 0;
           }
          } else {
            this.toast.toastError('No se pudo realizar la consulta, intentelo más tarde');
          }
        }, (err) => {
          this.toast.toastError('No se pudo realizar la consulta, intentelo más tarde');
        })
      }
      
      
    }
    }




  ionViewWillLeave() {
    this.salas.forEach((item) => {
      this.socket.emit('salidachat', {id: item._id});
    })
  }



}
